<template>
  <v-container fluid>
    <v-layout wrap row>
      <v-flex xs12 md10 offset-md1>
        <v-card>
          <v-card-title>
            Variation
            <v-spacer></v-spacer>
            <v-text-field
              v-model="search"
              append-icon="mdi-magnify"
              label="Search"
              single-line
              hide-details
            ></v-text-field>
          </v-card-title>
          <v-data-table :headers="headers" :items="variations" :search="search">
            <template v-slot:item.price="{ item }" slot-scope>
              <v-chip
                label
                :class="`active-${!item.status}`"
                @click="doUpdateStatus(item)"
                >${{ item.price }}</v-chip
              >
            </template>
            <template v-slot:item.price_sale="{ item }" slot-scope>
              <v-chip
                label
                :class="`active-${item.status}`"
                @click="doUpdateStatus(item)"
                >${{ item.price_sale }}</v-chip
              >
            </template>
            <template v-slot:item.productId="{ item }" slot-scope>
              <v-col class="d-flex" cols="12" sm="10">
                <v-select
                  v-model="item.productId"
                  :items="productItem"
                  :rules="[(v) => !!v || 'Item is required']"
                  required
                  dense
                  @change="changeProduct(item)"
                ></v-select>
              </v-col>
            </template>
            <template v-slot:item.id="{ item }" slot-scope>
              <v-flex xs12 mb-2>
                <v-btn class="red mr-1" small @click="doDelete(item.id)">
                  <v-icon small>delete</v-icon>Delete
                </v-btn>
              </v-flex>

              <v-flex xs12>
                <v-btn class="primary mr-1" small @click.stop="update(item)">
                  <v-icon small>system_update_alt</v-icon>Update
                </v-btn>
              </v-flex>
            </template>
          </v-data-table>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  data() {
    return {
      search: "",
      headers: [
        {
          text: "NAME",
          align: "start",
          sortable: false,
          value: "name",
        },
        { text: "PRICE", value: "price", sortable: true },
        { text: "PRICE_SALE", value: "price_sale", sortable: true },
        { text: "PRODUCT", value: "productId" },
        { text: "ACTION", value: "id", sortable: false },
      ],
    };
  },
  props: ["variations"],
  created() {
    this.getProducts();
  },
  computed: {
    ...mapGetters("product", ["getProduct"]),
    productItem() {
      return this.getProduct;
    },
  },
  methods: {
    ...mapActions("product", ["getProducts"]),
    ...mapActions("variation", ["updateStatus", "updateProduct"]),
    doUpdateStatus(item) {
      let notify = {
        color: "",
        text: "",
      };
      this.updateStatus(item)
        .then((res) => {
          (notify.color = "success"), (notify.text = res);
          this.$emit("Notify", notify);
        })
        .catch((err) => {
          (notify.color = "error"), (notify.text = err);
          this.$emit("Notify", notify);
        });
    },
    changeProduct(item) {
      let dataProductId = [
        {
          id: item.id,
          propName: "productId",
          value: item.productId,
        },
      ];
      let notify = {
        color: "",
        text: "",
      };
      this.updateProduct(dataProductId)
        .then((res) => {
          (notify.color = "success"), (notify.text = res);
          this.$emit("Notify", notify);
        })
        .catch((err) => {
          (notify.color = " error"), (notify.text = err);
          this.$emit("Notify", notify);
        });
    },
    doDelete(variationId) {
      let notify = {
        color: "error",
        text: "Are you sure ?",
        id: variationId,
      };
      this.$emit("Notify", notify);
    },
  },
};
</script>

<style scoped>
.active-true {
  border-left: 4px solid green;
}
.active-false {
  border-left: 4px solid red;
  text-decoration: line-through;
}
</style>
