<template>
  <div id="varitation">
    <h2 class="subheading">Variation</h2>
    <v-snackbar
      v-model="snackbar"
      :timeout="action ? 100000 : 3000"
      top
      right
      :color="color"
    >
      <span>{{ text }}</span>
      <v-btn v-if="action" text @click="doDelete(action)">Yes</v-btn>
      <v-btn
        text
        color="white"
        @click="snackbar = false"
        v-if="action ? (textBtn = 'No') : (textBtn = 'Close')"
        >{{ textBtn }}</v-btn
      >
    </v-snackbar>
    <NavBarVariation @Notify="notifi" />
    <v-container class="my-5">
      <DatatableVariation :variations="variations" @Notify="notifi" />
    </v-container>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import DatatableVariation from "@/components/Variation/DatatableVariation";
import NavBarVariation from "@/components/Variation/NavBarVariation";
export default {
  data() {
    return {
      snackbar: false,
      color: "",
      text: "",
      action: "",
      textBtn: "",
    };
  },
  components: {
    NavBarVariation,
    DatatableVariation,
  },
  created() {
    this.getListVariation();
  },
  computed: {
    ...mapState("variation", ["variations"]),
  },
  methods: {
    ...mapActions("variation", ["getListVariation", "deleteVariation"]),
    notifi(data) {
      this.snackbar = true;
      this.text = data.text;
      this.color = data.color;
      this.action = data.id;
    },
    doDelete(variationId) {
      this.deleteVariation(variationId)
        .then((res) => {
          this.action = "";
          this.snackbar = true;
          this.text = res;
          this.color = "success";
        })
        .catch((err) => {
          this.action = "";
          this.snackbar = true;
          this.text = err;
          this.color = "error";
        });
    },
  },
};
</script>

<style></style>
