<template>
  <v-container class="my-5">
    <v-layout row wrap align-center justify-space-around class="lighten-2" flat>
      <span class="mr-3 "><v-icon>dashboard</v-icon> Toolbar</span>
      <v-flex md2>
        <v-btn color="success" small @click.stop="loadDialog">
          <v-icon>add</v-icon>
          Add New</v-btn
        >
        <v-dialog
          max-width="700px"
          persistent
          v-model="dialog"
          transition="fab-transition"
          :retain-focus="false"
        >
          <v-card>
            <v-card-title>
              <h2>Add a New Variation</h2>
            </v-card-title>
            <v-card-text>
              <v-form class="px-2" ref="form" @submit.prevent="doAddVariation">
                <v-text-field
                  outlined
                  label="Name"
                  :rules="nameRules"
                  v-model="name"
                ></v-text-field>
                <v-text-field
                  outlined
                  label="Price"
                  :rules="numberRules"
                  v-model="price"
                ></v-text-field>
                <v-text-field
                  outlined
                  label="Price Sale"
                  :rules="numberRules"
                  v-model="price_sale"
                ></v-text-field>
                <v-select
                  outlined
                  :items="productItem"
                  :rules="[(v) => !!v || 'Category is required']"
                  v-model="productId"
                  label="Product"
                  :retain-focus="false"
                ></v-select>
                <v-switch v-model="status" label="Status"></v-switch>
                <!-- <input
				type="file"
				name="image"
				hidden
				ref="fileInput"
				accept="image/*"
				@change="onFilePicked"
				/> -->
                <!-- <v-flex md4 v-for="(image, index) in imageUrl" :key="index">
                  <img width="100px" :src="image" alt="" />
                </v-flex> -->

                <v-layout>
                  <v-flex md12 xs12 right>
                    <v-btn color="success" type="submit" :loading="loading"
                      >Add</v-btn
                    >
                    <v-btn class="ml-2" color="error" @click.prevent="close()"
                      >Done</v-btn
                    >
                  </v-flex>
                </v-layout>
              </v-form>
            </v-card-text>
          </v-card>
        </v-dialog>
      </v-flex>

      <v-flex md2>
        <v-btn color="info" small
          ><v-icon>vertical_align_top</v-icon> Export Exel</v-btn
        >
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  data() {
    return {
      dialog: false,
      loading: false,

      name: "",
      price: "",
      price_sale: "",
      status: false,
      productId: "",

      productItem: [],
      numberRules: [
        (v) =>
          (!isNaN(parseFloat(v)) && v >= 0 && v <= 999999) ||
          "Number has to be between 0 and 999",
        (v) => !!v || "Quantity is required",
        (v) =>
          (v && v.length <= 10) || "Quantity must be less than 10 characters",
      ],
      nameRules: [
        (v) => !!v || "Name is required",
        (v) => (v && v.length <= 25) || "Name must be less than 10 characters",
      ],
    };
  },

  created() {
    this.getProducts();
  },
  computed: {
    ...mapGetters("product", ["getProduct"]),
  },
  methods: {
    ...mapActions("product", ["getProducts"]),
    ...mapActions("variation", ["addVariation"]),
    loadDialog() {
      this.status = false;
      this.dialog = true;
      this.productItem = this.getProduct;
    },
    close() {
      this.dialog = false;
      this.$refs.form.reset();
    },
    doAddVariation() {
      const dataVariation = {
        name: this.name,
        price: this.price,
        price_sale: this.price_sale,
        status: this.status,
        productId: this.productId,
      };
      let notify = {
        color: "",
        text: "",
      };
      if (this.$refs.form.validate()) {
        this.addVariation(dataVariation)
          .then((res) => {
            (notify.color = "success"), (notify.text = res);
            this.$emit("Notify", notify);
            this.$refs.form.reset();
            this.loading = true;
            this.dialog = false;
            this.loading = false;
          })
          .catch((err) => {
            (notify.color = "error"), (notify.text = err);
            this.$emit("Notify", notify);
          });
      }
    },
  },
};
</script>

<style></style>
